d obsahuje dva standardn podporovan sloupce (<b>Title</b> a <b>Metric</b>) a dva dal sloupce (<b>Publisher</b> a <b>Success</b>). Aby bylo mon tato data sprvn uloit do databze, je teba definovat dva dal rozmry pro tyto sloupce pomoc tlatka <b>Pidat rozmr</b>. <br> Pokud u je dan rozmr pouit jinde v systmu, je mon jej pouze vybrat z nabzenho menu. V opanm ppad ho musme vytvoit. Pi vytven potebujeme pouze uvst nzev sloupce, pod kterm jsou data uvedena v souboru, tedy <b>Publisher</b> nebo <b>Success</b> v naem ppad. Meme tak rozmru piadit odlin jmno, pokud chceme aby byl pi rozbrazen v systmu prezentovn jinak - a to v etin i anglitin. V naem ppad se meme nap. rozhodnout, e pouijeme nzev <b>Dostupnost</b> pro sloupec <b>Success</b>."}}')
