<i18n lang="yaml">
en:
    part1: On this page, you can upload data for platforms which do not offer a SUSHI interface
           or older data which are not longer available through SUSHI.
    part2: For correct import of data, it is necessary to provide data in the
           <strong>right format</strong>. For platforms that support <strong>COUNTER</strong>, you
           can use COUNTER data saved in <strong>CSV</strong> file. For platforms that do not use
           COUNTER, you need to <strong>prepare data according to the rules below</strong>.
           Files in the Microsoft <strong>Excell format are not supported</strong>.
    non_counter_data_h: non-COUNTER data
    non_counter_data:
           Because of the large variability in formats that providers use to deliver non-COUNTER
           data, it is not possible to implement support for all of them. Instead, you have to convert
           the data into a common format described below.
           <br><br>
           <em>Note</em> - It is often most practical to prepare your data in a table editor
           and then export it into the CSV format with the correct parameters.
    ncd_file_format_h: File format
    ncd_file_format:
           For the data to be correctly imported, it is necessary to use the CSV format with
           the following parameters -
           comma (<code>,</code>) as field separator, double quotes (<code>"</code>) for text
           quotation.
    ncd_file_content_h: File content
    ncd_file_content:
           The first row of the table contains a header which describes the meaning of individual
           columns. Data for individual titles, metrics, etc. are stored in separate rows.
           If the file contains data for more than one month, they are stored as part of one
           row in separate columns.
           <br>
           Names of individual columns are defined by the report type used (more exactly the
           the dimensions it contains).
           List of dimensions for a specific report type will be displayed when it is selected from
           the selection in the fields "Standard dimensions" and "Report specific dimensions".
           Besides the columns for individual dimensions, columns for data for individual months
           are supported. These have to be named in the "Month Year" form, e.g. "Jan 2019"
           (month is specified using its English 3-letter abbreviation) or in
           the "YYYY-MM" format, e.g. "2019-01".
           <br><br>
           <em>Note</em> - the order of columns is not important, but their names must strictly
           match the report type specification.
    ncd_file_example_h: Example
    example_img_desc:
           Example with standard dimensions <i>Metric</i> and <i>Title</i> and specific
           dimensions <i>Publisher</i> and <i>Success</i>.

cs:
    part1: Tato strnka umouje nahrn dat k platformm, kter neposkytuj rozhran SUSHI a nebo
           starch dat, kter ji nejsou na platform v rmci SUSHI k dispozici.
    part2: Pro sprvn nahrn dat je nutn data nahrt ve  <strong>sprvnm formtu</strong>.
           Pro platformy, kter jej podporuj, mete data nahrt
           <strong>ve formtu COUNTER</strong> uloen do souboru ve formtu <strong>CSV</strong>.
           Pro platformy, kter nevyuvaj standardn formt COUNTER, je teba data
           <strong>pipravit podle ne uvedench pravidel</strong>.
           Soubory ve formtu Microsoft <strong>Excel nejsou podporovny</strong>.
    non_counter_data_h: Data mimo formt COUNTER
    non_counter_data:
           Vzhledem k velk rozmanitosti formt, ve kter poskytovatel dodvaj data mimo formt
           COUNTER nen mon implementovat jejich podporu. Je proto nutn pro import data pipravit
           do jednotnho formtu, kter je popsn ne.
           <br><br>
           <em>Poznmka</em> - Data je ideln pipravit v tabulkovm editoru a vyexportovat do
           formtu CSV se sprvnm nastavenm parametr ukldn.
    ncd_file_format_h: Formt souboru
    ncd_file_format:
           Pro import mus bt data uloena v textovm formtu CSV s nsledujcmi parametry -
           rka (<code>,</code>) jako oddlova pol, dvojit uvozovky (<code>"</code>) pro
           uvozen textu.
    ncd_file_content_h: Obsah souboru
    ncd_file_content:
           Prvn dek tabulky obsahuje jej hlaviku, kter uruje vznam jednotlivch sloupc.
           Data pro jednotliv tituly, metriky, atp. jsou uloena na samostatnch dcch.
           Pokud soubor obsahuje data za vce msc, jsou uloena vdy v rmci jednoho dku
           v samostatnch sloupcch.
           <br>
           Nzvy jednotlivch sloupc jsou dan typem reportu, resp. rozmry, kter obsahuje.
           Seznam rozmr se zobraz pi vbru konkrtnho typu reportu v polch
           "Standardn rozmry" a "Rozmry specifick pro report". Krom sloupc pro jednotliv
           rozmry jsou podporovny jet sloupce s daty pro jednotliv msce. Ty mus mt nzev
           v podob "Month Year", nap. "Jan 2019" (nzvy msc jsou tpsmenn anglick zkratky)
           a nebo "YYYY-MM", nap. "2019-01".
           <br><br>
           <em>Poznmka</em> - poad sloupc nen dleit, ale jejich nzvy mus pesn odpovdat
           specifikaci pro dan typ reportu.
    ncd_file_example_h: Ukzka
    example_img_desc:
           Ukzka se standardnmi rozmry <i>Metric</i> a <i>Title</i> a specifickmi
           rozmry <i>Publisher</i> a <i>Success</i>.


</i18n>

